.comment-container {
  position: relative; /* Added to position the delete button */
  border: 1px solid #ddd;
  margin: 10px 0;
  padding: 15px;
  border-radius: 8px;
  background-color: #f9f9f9;
}

.comment-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.comment-header strong {
  font-size: 16px;
  color: #333;
}

.comment-date {
  font-size: 12px;
  color: #888;
}

.comment-text, .comment-edit {
  font-size: 14px;
  color: #555;
  margin-bottom: 10px;
}

.comment-text {
  display: block;
}

.comment-edit {
  width: 100%;
  height: 60px;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  resize: none;
}

.comment-buttons {
  display: flex;
  gap: 10px;
}

/* Updated button styles for rounded delete button */
.comment-buttons button {
  padding: 5px 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
}

.comment-buttons .edit-button {
  background-color: #4caf50;
  color: white;
}

.comment-buttons .delete-button {
  background-color: #f44336;
  color: white;
}

.delete-button {
  position: absolute;
  top: -10px; /* Adjust to position it at the top border */
  right: -10px; /* Adjust to position it at the right border */
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f44336;
  color: white;
  font-size: 16px;
  border: none;
  cursor: pointer;
  text-align: center;
}

.delete-button:hover {
  background-color: #c62828;
}

/* Style for replies */
.reply-container {
  position: relative; /* Added to position the delete button */
  margin-left: 20px;
  border: 1px solid #ddd;
  padding: 10px;
  border-radius: 8px;
  background-color: #f1f1f1;
}

.reply-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.reply-header strong {
  font-size: 14px;
  color: #333;
}

.reply-date {
  font-size: 12px;
  color: #888;
}

.reply-text {
  font-size: 13px;
  color: #555;
}

.reply-buttons {
  display: flex;
  gap: 10px;
}

/* Updated button styles for rounded delete button */
.reply-buttons button {
  padding: 4px 8px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 13px;
}

.reply-buttons .edit-button {
  background-color: #4caf50;
  color: white;
}

.reply-buttons .delete-button {
  background-color: #f44336;
  color: white;
}

/* Position delete button on the border */
.reply-buttons .delete-button {
  position: absolute;
  top: -10px; /* Adjust to position it at the top border */
  right: -10px; /* Adjust to position it at the right border */
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  border: none;
  cursor: pointer;
}

/* Add hover effect for delete button */
.reply-buttons .delete-button:hover {
  background-color: #c62828;
}
